/*eslint-disable*/
import React from "react";

// reactstrap components
import {
  Container,
  NavItem,
  NavLink,
  Nav } from "reactstrap";

function DarkFooter() {
  return (
    <footer className="footer pb-3 pb-md-0" data-background-color="black">
      <Container className='footerContainer'>

        <Nav style={{display: 'inline'}}>
          <NavItem>
            <NavLink
              href="/index"
              style={{paddingTop: '0'}}
            >
              <p>Domů</p>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              href="/index/#services"
              style={{ paddingTop: '0' }}
            >
              <p>Co děláme</p>
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              href="/kontakt"
              style={{ paddingTop: '0' }}
            >
              <p>Kontakt</p>
            </NavLink>
          </NavItem>

        </Nav> 


        <div className="copyright" id="copyright">
          © {new Date().getFullYear()}, Milan Komprs{" "}
          . Created By{" "}
          <a
            href="mailto:tomas.komprs@gamil.com"
            onClick={(event) => event.preventDefault()}
          >
           Tomas Komprs
          </a>
          .
        </div>
      </Container>
    </footer>
  );
}

export default DarkFooter;
