import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

//Test/Fix for githubPages

// styles for this kit
import "assets/css/bootstrap.min.css";
import "assets/scss/now-ui-kit.scss";
import "assets/demo/demo.css";
import "assets/demo/nucleo-icons-page-styles.css";
// pages for this kit
import Index from "views/Index.js";
import ContactPage from "views/examples/ContactPage.js";
import FontanyPage from "views/examples/FontanyPage.js";
import VodaPlynPage from "views/examples/vodaPlynPage.js";




export default function Routes() {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Switch>
        <Switch>
          <Route
            path="/index"
            render={props => <Index {...props} />} />
          <Route
            path="/kontakt"
            render={props => <ContactPage {...props} />}
          />
          <Route
            path="/fontany"
            render={props => <FontanyPage {...props} />}
          />
          <Route
            path="/vodaPlyn"
            render={props => <VodaPlynPage {...props} />}
          />
          <Route
            path="/solary"
            render={props => <VodaPlynPage {...props} />}
          />
          <Route
            path="/krby"
            render={props => <VodaPlynPage {...props} />}
          />
          <Route
            path="/bazeny"
            render={props => <VodaPlynPage {...props} />}
          />
          <Route
            path="/vodaPlyn"
            render={props => <VodaPlynPage {...props} />}
          />
          <Route
            path="/upravnaVod"
            render={props => <VodaPlynPage {...props} />}
          />


          <Redirect to="/index" />
          <Redirect from="/" to="/index" />
        </Switch>
      </Switch>
    </BrowserRouter>
  );
}





ReactDOM.render(
  <Routes />,
  document.getElementById("root")
);
